import React from 'react';
import css from './ArroundCheckBox.module.css';

export default function ArroundCheckBox(props){

	// console.log(props);
	return (
			<div  className={css.checkBox}>
				<label><input type={'checkbox'}
					   id={'input'}
					   onChange={props.handleChangeArround} 
					   className={css.inputBox}
					   checked={props.checkArround}/>
				Окружение</label>
			</div>
		);
}