import React from 'react';
import css from '../NavButtons/NavButtons.module.css';


export default function ShipButtons({selectedShipItem,shipsCount,handleSelectItemClick}){
	let buttons=[1,2,3,4];
	return (
			buttons.map(item=><NavButtons key={item} 
										   id={item} 
										   style={(Number(selectedShipItem)===item)? css.selected:''} 
										   shipCount={shipsCount[item-1]}
										   nuberOfDec={5-item}
										   handleSelectItemClick={handleSelectItemClick}/>)
		);
}

function NavButtons({style,handleSelectItemClick,id,nuberOfDec,shipCount}){
	
	return (<div className={`${css.itemFour} ${style}`}>
				<div onClick={handleSelectItemClick} id={id}
					 className={css.text}>
					 {`${nuberOfDec} - ${'Палубы'}`}</div>
				<div className={css.number}>{shipCount}</div>
			</div>	
		);
}