// import React from 'react';
// import GridBoxContainer from '../GridBoxContainer/GridBoxContainer.jsx'
import GridFieldContainer from '../GridBoxUser/GridFieldContainer.jsx';
import {connect} from 'react-redux';
import {ACSelectedItemIdOpponent} from '../Reducers/ReducerFieldUser.js';



let mapStateToProps=(state)=>{
	return ({
		// userName:state.userGridState.opponentName,
		active:state.userGridState.active,
		userGrid:false,
		count:state.userGridState.countOpponent,
		checkArround:state.userGridState.checkArround,
		arrAroundArea:state.userGridState.shipAroundArea,
		arrAbstractShip:state.userGridState.shipAbstractArea,
		fieldUser:state.userGridState.fieldOpponent,
		isFieldCompleted:state.userGridState.isFieldCompleted,
		selectedShipItem:state.userGridState.selectedShipItem,
		cursorId:state.userGridState.cursorPosition
	})
};  

let mapDispatchToProps=(dispatcher)=>{
	return ({  
		onGridClick:(e)=>dispatcher(ACSelectedItemIdOpponent(e.target.id-1))
	})
};

const ContentOpponent=connect(mapStateToProps,mapDispatchToProps)(GridFieldContainer);

export default ContentOpponent