import React,{PureComponent} from 'react';
// import css from './NavHitBtn.css';


export default class NavHitBtn extends React.PureComponent{

	// constructor(props){
	// 	super(props);
	// }

	handleClick=()=>{
		if(this.props.active===true){
			this.props.handleOnShutClick();
		setTimeout(()=>this.props.handleWhoisNext(),1000);
		}	
	}
	componentDidUpdate(){
		if(this.props.active===false){
			let a=new Promise(resolve=>{
				setTimeout(()=>{
					this.props.handleOnMoveTransition();
					resolve();
				},1500);
			});
			a.then(result=>setTimeout(()=>{
				this.props.handleWhoisNext();
			},1500));
		}
	}

	render(){
		let style=null;
			if(this.props.active===false) style={cursor:'auto',opacity:'0.5'};
			return (
			<div className={this.props.style} style={style} data-title={this.props.text} onClick={this.handleClick}>
				<img  alt={this.props.alt} src={this.props.img} className={''}/>
			</div>
		);
	}
			
}