// import React from 'react';
import Navigation from './Navigation.jsx';
import {connect} from 'react-redux';
import {ACSelectedShipId,
		ACPutShipOnField,
		ACRotateClick,
		ACResetClick,
		ACCheckBoxClick,
		ACStartGame,
		ACTimeTick,
		AConSelectItem,
		ACStartFight
	   } from '../Reducers/ReducerFieldUser.js';
// import {} from '../Reducers/ReducerFieldUser.js';

let mapStateToProps=(state)=>{
	return ({
		rotate:state.userGridState.rotate,
		isFieldCompleted:state.userGridState.isFieldCompleted,
		shipsCount:state.userGridState.shipsCount,
		selectedShipItem:state.userGridState.selectedShipItem,
		checkArround:state.userGridState.checkArround,
		userMove:state.userGridState.userMove,
		opponentMove:state.userGridState.opponentMove,
		gameStarted:state.userGridState.gameStarted,
		whoIsNext:state.userGridState.whoIsNext,
		gameOver:state.userGridState.gameOver,
		strtFighting:state.userGridState.strtFighting,
		showHint:state.userGridState.showHint,
		hintText:state.userGridState.hintText,
		hintError:state.userGridState.hintError,
		gender:state.userGridState.gender,
		usersItems:state.userGridState.usersItems,
		time:state.userGridState.time,
		selectedUserItem:state.userGridState.selectedUserItem,
		arrowLeftRight:state.userGridState.arrowLeftRight
	});

};

let mapDispatchToProps=(dispatcher)=>{
	return ({
		handleChangeArround:(e)=>dispatcher(ACCheckBoxClick(e)),
		handleOnPlaceClick:()=>dispatcher(ACPutShipOnField()),
		handleSelectItemClick:(e)=>dispatcher(ACSelectedShipId(e.target.id)),
		handleRotateClick:()=>dispatcher(ACRotateClick()),
		handleOnResetClick:()=>dispatcher(ACResetClick()),
		handleOnStartClick:(isFieldCompleted)=>dispatcher(ACStartFight(isFieldCompleted)),
		handleTimeTick:()=>dispatcher(ACTimeTick()),
		handleSelectItem:(item)=>dispatcher(AConSelectItem(item)),
		handleOnStartGame:()=>dispatcher(ACStartGame())
	});

};


let NavigationContainer=connect(mapStateToProps,mapDispatchToProps)(Navigation)

export default  NavigationContainer