import React from 'react';
import css from './HintText.module.css';


let HintText=React.memo(({text,error})=>{
	let errorStyle={color:'black'};
	if(error) errorStyle={color:'red'};
	return(
			<div className={css.hintcontent}>
				 <div className={css.hint}>
				 	 <span style={errorStyle}>{text}</span>
				 </div>
			</div>
		);
});

export default HintText