import React from 'react';
import css from './AboutProject.module.css';
import gameImg from './game-img.jpg';
// import gamersImg from './gamers.jpg';
import navButtonsImg from './navbtn-2.jpg';
import navBtn from './nav-btn2.jpg';
import tableImg from './winners-img.png';
import loginImg from './login-img.png';
import {useState} from 'react';
import rulesText from './rulesText.js';



let RulesItem=({handleOnClick})=>{
	return (
			<div>
				<div className={css.backgroundFon}></div>
				<div className={css.rulesDialog}>
					<div className={css.rulesContent}>
						 <h2>Правила игры.</h2>
						 {rulesText.map((text,id)=>(<p key={id}>{text}</p>))}
					</div>
					<button onClick={handleOnClick} className={css.rulesOkButton}>ok</button>
				</div>
			</div>

		);
}

let AboutItemContainer=({lined,header1,header2,hdr2Color,text1,images,zoom=false,text2,buttonText,onClick})=>{
	let button=(buttonText!==undefined)? true:false;

	return(
			<div className={`${css.contentBlock} ${(lined)?css.line:''}`}>
				{header1 && <h1 className={css.header1}><span>{header1}</span></h1>}
				{header2 && <h2 className={css.header2} style={hdr2Color}>{header2}</h2>}
				<div className={css.textContent}>
					{text1.map((text,idx)=><p key={idx}>{text}</p>)}
					<div className={css.imageBlock}>
						{images.map((im,idx)=>{return (<img src={im} 
													  alt={'Изображение-'+idx} 
													  className={`${css.gameImg} ${(zoom)?css.zoom:''}`} 
													  key={idx}/>
													  )})
						}
					</div>
					{text2.map((text,idx)=>(<p className={css.simpleText} key={idx}>{text}</p>))}
				</div>
				{button && <div className={css.rulesButton} onClick={onClick}><span>{buttonText}</span></div>}
			</div>
		)
};


let AboutProject=(props)=>{

	let [showRules,setShowRules]=useState(false);

	let handleClick=()=>{
		// alert("Clicked");
		setShowRules(true);
	}

	let handleOnStartGame=()=>{
		props.history.push('/login');
	}
	let handleOnRulesClick=()=>{
		// alert('OK');
		setShowRules(false);
	}

	return (
			<div className={css.mainContainer}>
			{showRules && <RulesItem handleOnClick={handleOnRulesClick}/>}
				<AboutItemContainer lined={false} 
									header1={'"Морской Бой"'}
									text1={['Эта игра представляет собой классическую','форму игры " Морской Бой ".']}
									images={[gameImg]}
									text2={['С правилами игры можно ознакомиться здесь.']}
									buttonText={'Прочитать правила'}
									onClick={handleClick}
									/>
				<AboutItemContainer lined={true} 
									header2={'Простой интерфейс'}
									hdr2Color={{color:'#2297e0'}}
									text1={['Всего несколько кнопок навигации','дают возможность легко управлять']}
									images={[navButtonsImg,navBtn]}
									text2={[]}
									/>
				<AboutItemContainer lined={true} 
									header2={'История достижений игроков'}
									hdr2Color={{color:'#053452'}}
									text1={['На вкладке " Победители " вы сможете','увидеть достижения пользователей в таблице.']}
									images={[tableImg]}
									zoom={true}
									text2={['Здесь содержится информация о количестве произведённых','выстрелов, а так же время и занимаемая позиция.']}
									/>
				<AboutItemContainer lined={true} 
									header2={'Чтобы начать игру'}
									hdr2Color={{color:'orangered'}}
									text1={['Нужно зарегистрироваться на вкладке "Регистрация"','или выбрать уже имеющегося пользователя.']}
									images={[loginImg]}
									text2={['В форме нужно заполнить поля обозначенные','цифрами 1, 2 и 3.']}
									buttonText={'Заполнить форму и начать Игру'}
									onClick={handleOnStartGame}
									/>
			</div>
		);
};


export default AboutProject