import React from 'react';
import css from './Users.module.css';
import {connect} from 'react-redux';

import imgFemale from '../Navigation/user-femail.png';
import imgMale from '../Navigation/user-male.png';


function Item(props){
	return(
			<div className={css.rowBlock+' '+props.style}>
					<div className={css.rowItem}>
						<div className={css.imgBlock}>
							<img src={props.img} alt='user' className={css.userImg}/>
						</div>
						{props.userName}
						<span className={css.email}> {props.email}</span>
					</div>
					<div className={css.rowItem}>
						<span><div className={css.number}>{props.points}</div> </span>
						<span className={css.rowItemMiddleText}>очков за: {props.time}</span>
					</div>
					<div className={`${css.rowItem} ${css.rowItemRight}`}>{props.index}</div>
			</div>
		)
}


function Users({usersItems}){
let showItems=true;
if(usersItems.length===0) showItems=false;
return(
		<div className={css.container}>
			<div className={css.headerBlock}>
				<div className={css.hederItem}>Игрок</div>
				<div className={css.hederItem}>Результат</div>
				<div className={css.hederItem}>Позиция</div>
			</div>
			<div className={css.mainBlock}>
			 	{showItems && usersItems.map((it,idx)=>{
			 			let style=css.other;
			 			let img=imgFemale;
			 			if(it.gender==='male') img=imgMale;
			 			if(idx===0) style=css.first;
			 			if(idx===1) style=css.second;
			 			if(idx===2) style=css.third;
			 			if(it.time==='--:--') style=css.other;

			 			return <Item userName={it.name} 
			 						 time={it.time}
			 						 points={it.points} 
			 						 email={it.email}
			 						 index={idx+1} 
			 						 style={style} 
			 						 key={it.name} 
			 						 img={img}/>
			 		})
			 	}
			</div>
			<div></div>
		</div>
	)
}
let mapStateToProps=(state)=>({usersItems:state.userGridState.usersItems});
let UsersContainer=connect(mapStateToProps,null)(Users);

export default UsersContainer