import React from 'react';
import {connect} from 'react-redux';
import GridBoxUser from '../GridBoxUser/GridBoxUser.js';
import NavigationContainer from '../Navigation/NavigationContainer.jsx';
import css from './MainContainer.module.css';


function Main(props){
	// let contentRef=React.createRef();
	return (
			<div className={css.contentBody}>
				<GridBoxUser userName={props.userName} userGrid={true} count={props.countUser}/>
		 		{true && <NavigationContainer/>}
		 		<GridBoxUser userName={'Android'} userGrid={false} count={props.countOpponent}/>
		 	</div>
		);
}
let mapStateToProps=(state)=>{
	return({
		userName:state.userGridState.userName,
		countUser:state.userGridState.countUser,
		countOpponent:state.userGridState.countOpponent
	})
};
let MainContainer=connect(mapStateToProps,null)(Main);

export default MainContainer