import React from 'react';
import Header from './Header/Header.jsx';
// import ContentUser from './Content/ContentUser.jsx';
import MainContainer from './MainContainer/MainContainer.jsx';
import AboutProject from './AboutProject/AboutProject.jsx';
// import ContentOpponent from './Content/ContentOpponent.jsx';
import Footer from './Footer/Footer.jsx';
// import NavigationContainer from './Navigation/NavigationContainer.jsx';
import LoginContainer from './Login/Login.jsx';
import UsersContainer from './Users/Users.jsx';
// import GridBoxUser from './GridBoxUser/GridBoxUser.jsx';
import css from './App.module.css';
import {connect} from 'react-redux';

import {ACShowHintText,ACResetClick,
		ACAddUser,ACSetToStorage,
		ACLoadFromStorage} from './Reducers/ReducerFieldUser.js'
import {Route,Redirect} from 'react-router-dom';

import cubok from './images/cubok.png';
import backgroundImg from './images/sea-1.jpg';
// import backgroundImg from './images/sea-1.jpg';


class App extends React.Component {
	constructor(props){
		super(props);
		this.props.handleLoadStorage();
	}

	onDialogClick=()=>{
		// this.props.handleAddUser();
		this.props.handleAddToStorage();
		this.props.handleOnResetClick();
		// this.props.history.push('/users');
	}

	componentDidMount(){
		setTimeout(()=>this.props.handleShowHintText(),2000);

	}
	componentDidUpdate(){
		if(this.props.gameOver===true){
			// debugger;
			this.props.handleAddUser();
			console.log("---------game over....");
		}
	}

  render(){
  	// console.log('App');
  	return (
	    <div className={css.appContainer}>
	    {this.props.gameOver && <MessageDialog userName={this.props.userName} 
	    									   onClick={this.onDialogClick}
	    									   userPoints={this.props.userPoints}/>
	    }
	        <Header className={'header'} isLogined={this.props.isLogined}/>
	        <div className={css.content} style={{backgroundImage:`url(${backgroundImg})`}}> 
	        	<Route path='/main' component={MainContainer}/>
	        	<Route path='/login' component={LoginContainer}/>
	        	<Route path='/users' component={UsersContainer}/>
	        	<Route path='/aboutproject' component={AboutProject}/>
	        	<Redirect to='/aboutproject'/>
	 		</div>
	        <Footer/>
	    </div>

 	 );
	}
}


let MessageDialog=({userName,userPoints,onClick})=>{
	// debugger;
	return(
			<div>
				<div className={css.dialog}>
			    	<div className={css.dialogBlock}>
			    		<div className={css.dialogHeader}>Игра окончена!</div>
			    	</div>
			    	<div className={css.dialogBlock}>
			    		<div><img src={cubok}  alt='cubok' className={css.imgBlock}/></div>
			    		<div className={css.tableBlock}> 
			    			<div>Ваш результат</div>
			    			<div className={css.tableItems}>{userName} - {userPoints} выст.</div>
			    		</div>
			    	</div>
			    	<div className={css.dialogBlock}>
			    		<button className={css.gialogBtn} onClick={onClick}>Продолжить</button>
			    	</div>
			    </div>
			    <div className={css.frameDialog}></div>
			</div>
		)
};

let mapStateToProps=(state)=>({
	userPoints:state.userGridState.currentUserPoints,
	userName:state.userGridState.userName,
	showHint:state.userGridState.showHint,
	isLogined:state.userGridState.isLogined,
	gameOver:state.userGridState.gameOver
});

let mapDispatchToProps=(dispatcher)=>({
	handleShowHintText:()=>dispatcher(ACShowHintText()),
	handleOnResetClick:()=>dispatcher(ACResetClick()),
	handleAddUser:()=>dispatcher(ACAddUser()),
	handleAddToStorage:()=>dispatcher(ACSetToStorage()),
	handleLoadStorage:()=>dispatcher(ACLoadFromStorage())
});

let Apps=connect(mapStateToProps,mapDispatchToProps)(App);

export  default Apps




