import React from 'react';
import css from './Login.module.css';
import {requiredField,lengthField,requiredEmail,requireName} from './validate.js';
import {reduxForm,Field} from 'redux-form';
import {connect} from 'react-redux';
import {AConLogin,ACResetClick,ACSetToStorage} from '../Reducers/ReducerFieldUser.js';
// import {Redirect} from 'react-router-dom';
// import { Button } from 'antd';


const InputItem=React.memo(({input,meta,...props})=>{
	// console.log(meta);
	let error=!meta.valid && meta.touched;
	return(
			<div className={`${css.inputfieldBlock} ${error && css.inputError}`}>
				<input {...input} {...props}/>
				{error && <div className={css.errorField}>{meta.error}</div>
				}
			</div>
		)

});

const RadioItem=React.memo(({label,input,checked,meta,...props})=>{
	// console.log(meta);
	return (
			<div className={css.radioContent}>
			  <label><input {...props} {...input} checked={checked} id={label}/>{label}</label>
			</div>	
			
		)
});



const  UserForm=React.memo((props)=>{

	return(
			<div className={css.contentLogin}>
				<div className={css.blockLogin}>
					<div className={css.headerLogin}>
						Форма для регистрации.
					</div>
					<div>
						<form onSubmit={props.handleSubmit} className={css.formLogin}> 
								<div className={css.fieldText}> Введите ваше имя</div>
								<Field component={InputItem} 
									   name={'name'} 
									   type={'text'}
									   placeholder={'например: Петя Иванов'} 
									   validate={[requiredField,lengthField,requireName]}
									   />
								<div className={css.fieldText}> Введите ваш Email</div>
								<Field component={InputItem} 
									   name={'email'} 
									   type={'text'}
									   placeholder={'например: petea@gmail.com'} 
									   validate={[requiredField,lengthField,requiredEmail]}
									   />
								<div className={css.fieldText}>Выберите пренадлежность</div>
								<div  className={css.radioBlock}>
									<Field component={RadioItem}
										   name={'gender'}
										   type={'radio'}
										   value={'male'}
										   label={'Мужской'}
										   defaultChecked={true}
										   />
									<Field component={RadioItem}
										   type={'radio'}
										   name={'gender'}
										   value={'female'}
										   label={'Женский'}
										   />
								</div>
							 	<button className={css.submitBtn}>Подтвердить</button>
							
						</form>
					</div>
				</div>
			</div>
		)
});
// <Button type="warning" style={{width:'50%'}}>Button</Button>

const LoginReduxForm=reduxForm({
	form:'LoginForm'
})(UserForm);


class Login extends React.PureComponent{

formSubmit=(FormData)=>{

		this.props.onLoginSubmit(FormData);
		this.props.handleAddToStorage();
		this.props.restartGame();
		this.props.history.push('/main');
	}

render(){

		 return(<div>	
					<LoginReduxForm onSubmit={this.formSubmit}/>
				</div>
			)
	}
}

const mapDispatchToProps=(dispatcher)=>({
	onLoginSubmit:(FormData)=>dispatcher(AConLogin(FormData)),
	restartGame:()=>dispatcher(ACResetClick()),
	handleAddToStorage:()=>dispatcher(ACSetToStorage())
});
const mapStateToProps=(state)=>({isLogined:state.userGridState.isLogined});

const LoginContainer=connect(mapStateToProps,mapDispatchToProps)(Login);

export default LoginContainer