const SET_SELECTED_ITEM_ID='SET_SELECTED_ITEM_ID';
const SET_SELECTED_SHIP_ID='SET_SELECTED_SHIP_ID';
const ROTATE_ON_CLICK='ROTATE_ON_CLICK';
const CHECK_BOX_CLICK='CHECK_BOX_CLICK';
const RESET_ON_CLICK='RESET_ON_CLICK';
const ADD_USER='ADD_USER';

const SELECTED_ITEM_ID_OPPONENT='SELECTED_ITEM_ID_OPPONENT';
const START_FIGHT_CLICK='START_FIGHT_CLICK';
const SHUT_ON_CLICK='SHUT_ON_CLICK';
const STOP_GAME_CLICK='STOP_GAME_CLICK';
const START_GAME_CLICK='START_GAME_CLICK';
const ON_MOVE_TRANSITION='ON_MOVE_TRANSITION';
const WHO_IS_NEXT='WHO_IS_NEXT';
const SHOW_HINT_TEXT='SHOW_HINT_TEXT';
