const GET_FROM_STORAGE='GET_FROM_STORAGE';

const SHIP_POINT=1;
const SHIP_HITED=2;
const FILL_POINT=3;
const EMPTY_POINT=0;
const USER_MOVE=true;
const OPPONENT_MOVE=false;
const RIGHT_ARROW=false;
const LEFT_ARROW=true;

const USERS_STORAGE='BattleSeaUsers';
const FIRST_USER={name:'Иван',email:'ivan@gmail.com',shuts:28,time:'02:30',points:6650,gender:'male',id:0};

