const initialState={// userGridState
		fieldUser:new Array(100).fill(0),
		shipsCount:[1,2,3,4],
		userShips:[
			   {x:[0,1,2,3],ship:[[0,0,0,0]],pos:[[0,0,0,0]]},
			   {x:[0,1,2],ship:[[0,0,0],[0,0,0]],pos:[[0,0,0],[0,0,0]]},
			   {x:[0,1],ship:[[0,0],[0,0],[0,0]],pos:[[0,0],[0,0],[0,0]]},
			   {x:[0],ship:[[0],[0],[0],[0]],pos:[[0],[0],[0],[0]]}
			  ],
		fieldOpponent: new Array(100).fill(0),
		opponentShips:[
			   {x:[0,1,2,3],ship:[[0,0,0,0]],pos:[[0,0,0,0]]},
			   {x:[0,1,2],ship:[[0,0,0],[0,0,0]],pos:[[0,0,0],[0,0,0]]},
			   {x:[0,1],ship:[[0,0],[0,0],[0,0]],pos:[[0,0],[0,0],[0,0]]},
			   {x:[0],ship:[[0],[0],[0],[0]],pos:[[0],[0],[0],[0]]}
			  ],
		usersItems:[],			
		selectedUserItem:0,
		// newUser:{name:'Ivan',shuts:0,time:'00:00',gender:'male',email:'nouser@nothing.com'},
		hintText:hintTextArray[0],  
		virtualPoints:[],
		memoKiledPoints:[],
		active:false,
		userName:'Иван',
		shipAbstractArea:[0],
		shipAroundArea:[0],
		cursorId:44,
		selectedShipItem:0,
		rotate:false,
		checkArround:false,
		isFieldCompleted:false,
		gameStarted:false,
		whoIsNext:true,
		userMove:false,
		arrowLeftRight:false,
		countUser:0,
		countOpponent:0,
		cursorPosition:44,
		opponentName:'Android',
		strtFighting:false,///    false
		gameOver:false,

		showHint:false,
		hintError:false,
		isLogined:false,
		email:'',
		gender:'male',
		time:'00:00',
		currentUserPoints:0
	};
