function showItemsOnGridField(state){

	if(state.checkArround && state.selectedShipItem>0){
			let ship=state.userShips[state.selectedShipItem-1].x;
			//findAroundItems([fieldUser],[userShip],cursor,isUser=false,isRotate=false)
			let	arrays=localAPI.findAroundItems(state.fieldUser,ship,state.cursorId,true,state.rotate);		
				state.shipAbstractArea=[...arrays.arrAbstractShip];
				state.shipAroundArea=[...arrays.arrAroundErea];
		}
		else if(state.selectedShipItem>0){
			let ship=state.userShips[state.selectedShipItem-1].x;
			//findAroundItems([fieldUser],[userShip],cursor,isUser=false,isRotate=false)
			let	arrays=localAPI.findAroundItems(state.fieldUser,ship,state.cursorId,true,state.rotate);	

				state.shipAbstractArea=[...arrays.arrAbstractShip];
				state.shipAroundArea=[...arrays.arrAroundErea];
				// console.log('Abstract ship...',state.shipAbstractArea);
		}

  return state;

}
//whoIsNext
// ==========================================================================

let ReducerFieldUser=(state=initialState,action)=>{
	let stateCopy={...state
				   // virtualPoints:[...state.virtualPoints]
				  };
	// console.log(stateCopy);
	if(action.type===START_GAME_CLICK){
		stateCopy.gameStarted=true;
		stateCopy.hintText=hintTextArray[1];
	}
	else if(action.type===GET_FROM_STORAGE){
		let LS=localStorage;
		let usersItems=[...stateCopy.usersItems]
		let users=JSON.parse(LS.getItem(USERS_STORAGE));
		if(users){
			users.forEach(i=>usersItems.push(i));
			stateCopy.usersItems=localAPI.sortUsersByPlace(usersItems);
		}
		else stateCopy.usersItems.push(FIRST_USER);
	}
	else if(action.type===SET_TO_STORAGE){
		let LS=localStorage;
		LS.setItem(USERS_STORAGE,JSON.stringify(stateCopy.usersItems));
	}
	else if(action.type===SELECT_USER_ITEM){
		let userId=action.value;
		let currentUser=stateCopy.usersItems.find(i=>i.id===Number(userId));
		stateCopy.userName=currentUser.name;
		stateCopy.selectedUserItem=userId;
		if(currentUser.gender==='male') stateCopy.gender='male';
		else stateCopy.gender='female';
	}
	else if(action.type===ADD_USER){
		let usersItems=[...stateCopy.usersItems];
		let currentUser=usersItems.find(i=>i.id===Number(stateCopy.selectedUserItem));
		if(currentUser){currentUser.time=stateCopy.time;
			currentUser.shuts=stateCopy.countOpponent;

			currentUser.points=localAPI.calculatePoints(currentUser);
			stateCopy.currentUserPoints=currentUser.points;
			stateCopy.usersItems=localAPI.sortUsersByPlace(usersItems);	
		}
	}
	else if(action.type===TIME_TICK){
		// console.log('Time Tick -----');
		let time=localAPI.getTimeTick(stateCopy.time);
		stateCopy.time=time;
	}
	else if(action.type===SHOW_HINT_TEXT){
		stateCopy.showHint=true;
	}
	else if(action.type===ON_LOGIN_SUBMIT){
		let usersItems=[...stateCopy.usersItems];
		let newUser={};
			newUser.name=action.FormData.name.split('')[0].toUpperCase()+action.FormData.name.split('').slice(1).join('');
			newUser.email=action.FormData.email;
			newUser.shuts=0;
			newUser.time='--:--';
			newUser.points=0;
			newUser.gender=(action.FormData.gender!==undefined)? action.FormData.gender:'male';
			newUser.id=usersItems.length;
		
		let result=localAPI.addUserToList(usersItems,newUser);
		if(!result) usersItems.push(newUser);
			stateCopy.usersItems=usersItems;
	}
	else if(action.type===WHO_IS_NEXT){
		// console.log('change move to ...',stateCopy.whoIsNext);
		if(stateCopy.whoIsNext===USER_MOVE) {
			stateCopy.arrowLeftRight=RIGHT_ARROW;
			if(!stateCopy.gameOver) stateCopy.hintText=hintTextArray[8];
		}
		else {	stateCopy.arrowLeftRight=LEFT_ARROW;
				if(!stateCopy.gameOver) stateCopy.hintText=hintTextArray[9];
			}
	}
	else if(action.type===RESET_ON_CLICK){
			// console.log('Start game...');
			stateCopy.fieldUser=[...localAPI.clearField(stateCopy.fieldUser)];
			stateCopy.fieldOpponent=[...localAPI.clearField(stateCopy.fieldOpponent)];
			stateCopy.rotate=false;
			stateCopy.checkArround=false;
			stateCopy.whoIsNext=true;
			stateCopy.arrowLeftRight=false;
			stateCopy.strtFighting=false;
			stateCopy.gameOver=false;
			stateCopy.gameStarted=false;
			stateCopy.active=false;
			stateCopy.countUser=0;
			stateCopy.cursorId=44;
			stateCopy.countOpponent=0;
			stateCopy.selectedShipItem=0;
			stateCopy.hintText=hintTextArray[0];
			stateCopy.shipAbstractArea=[0];
			stateCopy.shipAroundArea=[0];
			// stateCopy.gameStarted=false;
			stateCopy.shipsCount=[1,2,3,4];	
			stateCopy.isFieldCompleted=false;
			stateCopy.time='00:00';
	}
	else if(action.type===CHECK_BOX_CLICK){
		if(!stateCopy.isFieldCompleted){
			stateCopy.checkArround=!stateCopy.checkArround;	
			showItemsOnGridField(stateCopy);
		}		
	}	
	else if(action.type===SET_SELECTED_ITEM_ID){
		
		if(!stateCopy.isFieldCompleted){
			stateCopy.cursorId=action.id;// сохраняем новое положение курсора
			stateCopy=showItemsOnGridField(stateCopy);
		}
	}
	else if(action.type===ROTATE_ON_CLICK){
		if(!stateCopy.isFieldCompleted){
			stateCopy.rotate=!state.rotate;	//поворачиваем элемент
			stateCopy=showItemsOnGridField(stateCopy);
		}
	}
	else if(action.type===SET_SELECTED_SHIP_ID){

		if(!stateCopy.isFieldCompleted && stateCopy.shipsCount[action.id-1]!==0){
			stateCopy.selectedShipItem=action.id; // выбираем тип корабля
			stateCopy=showItemsOnGridField(stateCopy);
			// console.log('Select item...',action.id);
		}	
		
	}
	else if(action.type===SET_SHIP_ON_FIELD){
		let shipId=stateCopy.selectedShipItem;
		stateCopy.shipsCount=[...state.shipsCount];
		if(shipId>0  && !stateCopy.isFieldCompleted){
			stateCopy.fieldUser=[...state.fieldUser];

			let ship=stateCopy.userShips[shipId-1].x;
			//findAroundItems([fieldUser],[userShip],cursor,isUser=false/true,isRotate=false)
			let {isPossible}=localAPI.findAroundItems(stateCopy.fieldUser,ship,stateCopy.cursorId,true,stateCopy.rotate);	
			if(isPossible===true){
				    //заносим результат в поле игрока
					localAPI.setItemShipToField(stateCopy);
					stateCopy.shipsCount[shipId-1]--;
				//проверка окончания редактирования поля или выбор след. корабля
				if(stateCopy.shipsCount[shipId-1]===0){
					let id=localAPI.findNextItemButton(stateCopy.shipsCount);
					if(id===false){
						stateCopy.isFieldCompleted=true;
						stateCopy.checkArround=false;
						stateCopy.hintText=hintTextArray[2];
					}
					else stateCopy.selectedShipItem=id+1;
				}
				stateCopy.hintText=hintTextArray[1];
				stateCopy.hintError=false;
			}
			else {
					stateCopy.hintText=hintTextArray[3];
					stateCopy.hintError=true;
			}
		}
	}
	else if(action.type===STOP_GAME_CLICK){
		stateCopy.gameOver=false;
	}
	else if(action.type===ON_MOVE_TRANSITION){
		let winnerAndroid=false;
		let flagKill=false;
			// console.log('now Android should move...');
			let posId=localAPI.getHitPosition(stateCopy.fieldUser,stateCopy.virtualPoints,stateCopy.memoKiledPoints);
			if(posId!==null){
				//находим попадание в корабль
				let [currentShip,coordinates,result]=localAPI.findHitedShip(stateCopy.userShips,posId);
				
				// console.log(currentShip,result);
				stateCopy.fieldUser=[...state.fieldUser]; //копия объекта
				//определяем попадание в корабль
				if(result===true){
					stateCopy.fieldUser[posId]=SHIP_HITED;
					// при попадании определяем массив будущих мишеней
					if(stateCopy.virtualPoints.length>0) stateCopy.memoKiledPoints.push(posId);
					if(stateCopy.virtualPoints.length===0) stateCopy.virtualPoints=[...localAPI.getVirtualPoints(posId)];
					flagKill=!(currentShip.some(i=>i!==null));// убил или ранил
		  			if(flagKill){
		  				// console.log(coordinates,'--убил этот');
		   				localAPI.fillKilledShip(coordinates,stateCopy.fieldUser)
		  			}
					//определяем если корабль убит
					// console.log('А я попал!!!!',stateCopy.fieldUser);
					winnerAndroid=localAPI.checkWinner(stateCopy.userShips);
				    if(winnerAndroid===false) stateCopy.userMove=!stateCopy.userMove;
				}
				else {
						stateCopy.fieldUser[posId]=FILL_POINT;
						stateCopy.whoIsNext=USER_MOVE;
				}
				stateCopy.countUser++;
			}
			else {alert('Игра окончена!!!');stateCopy.gameOver=true;stateCopy.gameStarted=false;}
		  if(winnerAndroid===true)	 {
		  	     stateCopy.gameOver=true;
		  	     stateCopy.gameStarted=false;
		  		stateCopy.hintText=hintTextArray[4];}
	}
//==================Opponent=================================================
	else if(action.type===SELECTED_ITEM_ID_OPPONENT){
		stateCopy.cursorPosition=action.id;
		// console.log('click..',stateCopy.cursorPosition);
	}
	else if(action.type===START_FIGHT_CLICK){
	  if(action.isFieldCompleted===true){
	  	
	  	const TRY_COUNT=10;
	  	let tryBuildFlag=true;
	  	let count=0;
		//пытаемся построит поле элементов
		while(count<TRY_COUNT){
			// если удалось разместить элемент возвращаем ЛОЖЬ, если нет то ИСТИНУ
			tryBuildFlag=stateCopy.opponentShips.some((item,index)=>{
				var areas;
				for(let j=0;j<index+1;j++){
					// у нас есть несколько попыток разместить элемент
					areas=localAPI.findAroundItems(stateCopy.fieldOpponent,item.x);
					if(areas.isPossible===true){
							// console.log('ship is:',areas.arrAbstractShip);
							// если удалось разместить элемент, то переносим его в поле
							areas.arrAbstractShip.forEach(it=>stateCopy.fieldOpponent[it]=SHIP_POINT);
								[...item.ship[j]]=areas.arrAbstractShip;
								[...item.pos[j]]=areas.arrAbstractShip;	
					}
					else{  // если не удалось разместить элемент, перестраиваем всё поле
						   // очищаем поле
							stateCopy.fieldOpponent.forEach(it=>it=EMPTY_POINT); 
						return true;
					}
				}
				return false;
			});
			// debugger;
			if(tryBuildFlag===true) count++;
			else count=TRY_COUNT;		
		}
			// console.log('All ships: ',stateCopy.userShips);	
		stateCopy.strtFighting=true;
		stateCopy.active=true;
		stateCopy.hintText=hintTextArray[5];
	 }
	}
	else if(action.type===SHUT_ON_CLICK){
		if(stateCopy.whoIsNext===USER_MOVE){
			stateCopy.fieldOpponent=[...state.fieldOpponent];
			let flagKill=false;
			let winnerUser=false;
			let cursor=stateCopy.cursorPosition;
			let currentPoint=stateCopy.fieldOpponent[cursor];
			if(currentPoint===SHIP_POINT){
			   currentPoint=SHIP_HITED;

			   var [currentShip,coordinates]=localAPI.findHitedShip(stateCopy.opponentShips,cursor);
			   // убил или ранил
			   if(currentShip.length>0) flagKill=!currentShip.some(i=>i!==null);
			   	else flagKill=false;
			   if(flagKill)	 localAPI.fillKilledShip(coordinates,stateCopy.fieldOpponent)
			   // победил или нет
			   winnerUser=localAPI.checkWinner(stateCopy.opponentShips);
			   stateCopy.hintText=hintTextArray[6];
			   stateCopy.countOpponent++;
			}
			else if(currentPoint===EMPTY_POINT){
				currentPoint=FILL_POINT;
				stateCopy.userMove=!stateCopy.userMove;
				stateCopy.whoIsNext=OPPONENT_MOVE;
				stateCopy.countOpponent++;
			}
			if(winnerUser===true) {
				stateCopy.hintText=hintTextArray[7];
				stateCopy.gameOver=true;
		  	    stateCopy.gameStarted=false;

			} 
			stateCopy.fieldOpponent[cursor]=currentPoint;
			
		}
	}
	return stateCopy;
};

// ==========================================================================
export default ReducerFieldUser;

export let ACSelectedItemId=(id)=>({type:SET_SELECTED_ITEM_ID,id:id});
export let ACSelectedShipId=(id)=>({type:SET_SELECTED_SHIP_ID,id:id});
export let ACPutShipOnField=(id)=>({type:SET_SHIP_ON_FIELD,id:id});
export let ACRotateClick=()=>({type:ROTATE_ON_CLICK});
export let ACCheckBoxClick=()=>({type:CHECK_BOX_CLICK});
export let ACResetClick=()=>({type:RESET_ON_CLICK});
export let ACStopGame=()=>({type:STOP_GAME_CLICK});
export let ACStartGame=()=>({type:START_GAME_CLICK});
export let ACWhoisNext=(user)=>({type:WHO_IS_NEXT,user});
export let ACMoveTransition=(opponentMove)=>({type:ON_MOVE_TRANSITION,opponentMove});


export let ACSelectedItemIdOpponent=(id)=>({type:SELECTED_ITEM_ID_OPPONENT,id:id});
export let ACStartFight=(flag)=>({type:START_FIGHT_CLICK,isFieldCompleted:flag});
export let ACShut=(userMove)=>({type:SHUT_ON_CLICK,userMove:userMove});

export let ACShowHintText=()=>({type:SHOW_HINT_TEXT});
export let AConLogin=(FormData)=>({type:ON_LOGIN_SUBMIT,FormData});
export let ACAddUser=()=>({type:ADD_USER});
export let ACTimeTick=()=>({type:TIME_TICK});
export let AConSelectItem=(value)=>({type:SELECT_USER_ITEM,value});

export let ACSetToStorage=()=>({type:SET_TO_STORAGE});
export let ACLoadFromStorage=()=>({type:GET_FROM_STORAGE})

