import React,{PureComponent} from 'react';
import css from './Navigation.module.css';
import {connect} from 'react-redux';
// import {Route,Redirect} from 'react-router-dom';
// import NavButtons from './NavButtons/NavButtons.jsx';
import ShipButtons from './ShipButtons/ShipButtons.jsx';
import NavFunctionBtn from './NavFunctionBtn/NavFunctionBtn.jsx';
import NavHitBtn from './NavFunctionBtn/NavHitBtn.jsx';
import ArroundCheckBox from './ArroundCheckBox/ArroundCheckBox.jsx';
import HintText from './HintText/HintText.jsx';

import {ACWhoisNext,
		ACMoveTransition} from '../Reducers/ReducerFieldUser.js';
import {ACShut} from '../Reducers/ReducerFieldUser.js';

import rotateImg from './NavButtons/rotate.png';
import placeinImg from './NavButtons/placein.png';
import playImg from './NavButtons/play.png';
import resetImg from './NavButtons/reset.png';
import stopImg from './NavButtons/stop-1.png';
import targetImg from './NavButtons/target.png';
import arrowImg from './NavButtons/arrow-right.png';
import android from './android.png';
import userMale from './user-male.png';
import userFemale from './user-femail.png';


let UsersList=({usersItems,handleSelectItem,currentUserId})=>{
	// console.log('user.....',currentUserId);
	let listWrapper=null;
	if(usersItems.length>0) 
		listWrapper=usersItems.map(i=>{return <option key={i.id} 
				         value={i.id}>{i.name} </option>});

	return(
			<div className={css.selectBlock}>
				<label>Выбрать игрока</label>
				<select className={css.selectItem} 
						onChange={(e)=>handleSelectItem(e)} 
						defaultValue={currentUserId}
						name={'userName'}>
				{listWrapper}
				</select>
			</div>
		)
}

let TimeBar=({time})=>{

	return(
			<div className={css.timeContent}>
				<div className={css.timeBar}>
					<div className={css.timeLabel}>Время</div>
					<time>{time}</time>
				</div>
			</div>
		)
}


let GamerPhoto=React.memo(({whoIsFoto,gender})=>{
	// console.log('Gender...',gender);
	return (
				<div className={css.imgBlock}>
					{whoIsFoto && <img  src={android} alt='user' className={css.imageItem}/>}
					{!whoIsFoto && 
						<img  src={(gender==='male')? userMale:userFemale} alt='user' className={css.imageItem}/>
					}
				</div>

	)
});

let CancleGameDialog=React.memo((props)=>{
	return (
				<div>
					<div className={css.dialog}> 
						<div className={css.dialogHead}>Стоп ИГРА!</div>
						<div className={css.dialogContent}>Вы дейсвтиельно хотите прервать игру?</div>
						<div className={css.dialogFooter}>
							<button className={css.dialogButton} name='ok'onClick={(e)=>props.onClick(e)}>Подтвердить</button>
							<button className={css.dialogButton} name='no' onClick={(e)=>props.onClick(e)}>Отмена</button>
						</div>
					</div>
					<div className={css.dialogFrame}> </div>
				</div>
			);
	});


export default class Navigation extends React.PureComponent{
	
	constructor(props){
		super(props);
		this.style='';
		this.textFuncButtons=['Повернуть корабль',
						 'Разместить корабль',
						 'Начать игру',
						 'Сбросить игру',
						 'Стоп Игра!',
						 'Выстрел'];
		this.state={showDialog:false};
		this.timeId=(props.strtFighting)? setInterval(()=>{
			// alert('set Timer')
			props.handleTimeTick();
		},1000):null; 
	}

	handleSelectItem=(e)=>{
		this.props.handleSelectItem(e.target.value);
	}
	
	onStartClick=()=>{
		this.props.handleOnStartClick(this.props.isFieldCompleted);

		let tick=()=> {
			this.props.handleTimeTick();
  			this.timeId = setTimeout(tick, 1000);
		}

		this.timeId = setTimeout(tick, 1000);
	}
	onStopClick=()=>{
		this.setState({showDialog:true})
	}
	handleOnStartGame=()=>{
		this.props.handleOnResetClick();
		this.props.handleOnStartGame();
		///need to delete
		/// this.props.handleOnStartClick(true);
	}
	handleOnClickBtn=(e)=>{
		if(e.currentTarget.name==='ok') {
			this.props.handleOnResetClick();
		}
		this.setState({showDialog:false})
		clearTimeout(this.timeId);
	}

	componentWillUnmount(){
		clearTimeout(this.timeId);
		// alert('Unmount');
	}
	componentDidUpdate(){
		if(this.props.gameOver===true) clearTimeout(this.timeId);
	}
	
	render(){
		if(this.props.rotate) this.style=`${css.rotateActive} ${css.title}`;
		else this.style=css.title;
		// console.log('aaaaa...',this.showDialog);whoIsNext
		return(
			<div className={css.navblock}>
				{this.props.strtFighting &&  this.props.gameStarted && <TimeBar  time={this.props.time}/>} 
				{this.state.showDialog  && <CancleGameDialog onClick={this.handleOnClickBtn}/>}
				{(this.props.gameOver || !this.props.gameStarted) && <div className={css.navbtncontent}>
						
						<button className={css.startbtn} onClick={this.handleOnStartGame}>Начать Игру</button>
						<UsersList usersItems={this.props.usersItems} 
								   handleSelectItem={this.handleSelectItem}
								   currentUserId={this.props.selectedUserItem}/>
					</div>
				} 
				{!this.props.strtFighting && this.props.gameStarted && <div className={css.itemsBlock}>
							<ShipButtons handleSelectItemClick={this.props.handleSelectItemClick}
								 		 shipsCount={this.props.shipsCount}
										 selectedShipItem={this.props.selectedShipItem} />	
							<ArroundCheckBox handleChangeArround={this.props.handleChangeArround} 
											 checkArround={this.props.checkArround}/>
				         </div>
				 }

				{!this.props.strtFighting && this.props.gameStarted && <div className={css.funcBlock}>
					{!this.props.isFieldCompleted && <NavFunctionBtn handleClick={this.props.handleRotateClick}
								alt={'rotate'}
								style={(this.props.isFieldCompleted===true)? css.disactive:this.style}
								img={rotateImg}
								text={this.textFuncButtons[0]} />}
					{!this.props.isFieldCompleted && <NavFunctionBtn handleClick={this.props.handleOnPlaceClick}
								alt={'place_in'}
								style={(this.props.isFieldCompleted===false)? css.title:css.disactive}
								img={placeinImg}
								text={this.textFuncButtons[1]} />}
					{this.props.isFieldCompleted && <NavFunctionBtn handleClick={this.onStartClick}
								alt={'start'}
								style={(this.props.isFieldCompleted===true)? css.title:css.disactive}
								img={playImg}
								text={this.textFuncButtons[2]} />}
					<NavFunctionBtn handleClick={this.props.handleOnResetClick}
								alt={'reset'}
								style={css.title}
								img={resetImg}
								text={this.textFuncButtons[3]} />
				 </div>
				}
				{
				  (!this.props.gameStarted || this.props.strtFighting) &&	<GamerPhoto whoIsFoto={this.props.arrowLeftRight} gender={this.props.gender}/>
				}
				{this.props.strtFighting &&  this.props.gameStarted && <div className={css.gameBtnsBlock}>

						<div className={css.blockArrow}>
							{this.props.arrowLeftRight && <img src={arrowImg} alt='left' className={css.arrowLeft} />}
						</div>
						<NavFunctionBtn handleClick={this.onStopClick}
										alt={'stop_game'}
										style={`${css.title} ${css.titleFirst}`}
										img={stopImg}
										text={this.textFuncButtons[4]} />
						<NavHitButton active={this.props.whoIsNext}/>
						<div className={css.blockArrow}>
							{!this.props.arrowLeftRight && <img src={arrowImg} alt='right' className={css.arrowRight} />}
						</div>
					</div>
				}
				{
				  this.props.showHint &&	<HintText error={this.props.hintError} text={this.props.hintText}/>
				 }
			</div>
		)
	}
	
}
let mapStateToProps=(state)=>({
	alt:'shut',
	img: targetImg,
	userMove:state.userGridState.userMove,
	style:css.title,
	text:'Выстрел',
});
let mapDispatchToProps=(dispatcher)=>({
	handleOnShutClick:(userMove)=>dispatcher(ACShut(userMove)),
	handleWhoisNext:()=>dispatcher(ACWhoisNext()),
	handleOnMoveTransition:()=>dispatcher(ACMoveTransition())
});

let NavHitButton=connect(mapStateToProps,mapDispatchToProps)(NavHitBtn);

