import GridFieldContainer from '../GridBoxUser/GridFieldContainer.jsx';
// import Navigation from '../Navigation/Navigation.js';
import {connect} from 'react-redux';
import {ACSelectedItemId} from '../Reducers/ReducerFieldUser.js';


let mapStateToProps=(state)=>{
	return ({
		// userName:state.userGridState.userName,
		active:!state.userGridState.active,
		userGrid:true,
		count:state.userGridState.countUser,
		checkArround:state.userGridState.checkArround,
		arrAroundArea:state.userGridState.shipAroundArea,
		arrAbstractShip:state.userGridState.shipAbstractArea,
		fieldUser:state.userGridState.fieldUser,
		isFieldCompleted:state.userGridState.isFieldCompleted,
		selectedShipItem:state.userGridState.selectedShipItem,
		cursorId:state.userGridState.cursorId
	})
};

let mapDispatchToProps=(dispatcher)=>{
	// debugger;
	return ({
		onGridClick:(e)=>dispatcher(ACSelectedItemId(e.target.id-1))
	})
};

// const ContentUser=connect(mapStateToProps,mapDispatchToProps)(GridBoxContainer);
const ContentUser=connect(mapStateToProps,mapDispatchToProps)(GridFieldContainer);

export default ContentUser


