import React from 'react';
// import css from './NavFunctionBtn.module.css';



let NavFunctionBtn=React.memo((props)=>{

	return (
			<div className={props.style} data-title={props.text} onClick={()=>props.handleClick()}>
				<img  alt={props.alt} src={props.img} className={''}/>
			</div>
		);
});

export default NavFunctionBtn