import React from 'react';
import {NavLink} from 'react-router-dom';
// import {connect} from 'react';

import css from './Header.module.css';
import slide1 from './slide1.jpg';
import slide2 from './slide2.jpg';
import slide3 from './slide3.jpg';


let  Navigation=(props)=>{
	let headerRef=React.createRef();
	let style=(true)? css.link:css.disabled;
	let handleClick=()=>{
		let elementOffset=headerRef.current.offsetTop+headerRef.current.offsetHeight;
		// console.log('Ref...',);
		window.scrollTo(0,elementOffset);
	}
	return(
			<div className={css.bottomBlock} ref={headerRef}>
				<NavLink  to='/aboutproject' activeClassName={css.activeLink} className={css.link} onClick={handleClick}>
					<nav className={css.navlink} disabled>О проекте</nav>
				</NavLink>
				<NavLink to='/login' activeClassName={css.activeLink} className={css.link} onClick={handleClick}>
					<nav className={css.navlink}>Регистрация</nav>
				</NavLink>
				<NavLink to='/main' activeClassName={css.activeLink} className={style} onClick={handleClick}>
					<nav className={css.navlink}>Начать Игру</nav>
				</NavLink>	
				<NavLink  to='/users' activeClassName={css.activeLink} className={css.link} onClick={handleClick}>
					<nav className={css.navlink} disabled>Победители</nav>
				</NavLink>		
			</div>

		)
}

// const mapStateToProps=(state)=>({isLogined:state.userGridState.isLogined});
// const mapDispatchToProps=(dispatcher)=>({});
// let NavContainer=connect(mapStateToProps,mapDispatchToProps)(Navigation);

class SlideBlock extends React.Component{

	constructor(props){
		super(props);
		this.state={shiftLeft:0};
		this.timerId=null;
	}

	componentDidMount(){
		let slideTick=()=>{
				if(this.state.shiftLeft-400>-1200){
					this.setState({shiftLeft:this.state.shiftLeft-400});
				}
				else this.setState({shiftLeft:0});
				this.timerId=setTimeout(slideTick,4000);
			}
		this.timerId=setTimeout(slideTick,4000);
	}
	componentWillUnmount(){
		clearTimeout(this.timerId);
	}

	render(){
		return(
			<div className={css.topBlock}>
				<div className={css.textBlockLeft}><div className={css.innerTextBlock}>Морская</div></div>
				<div className={css.slideContent}>
					<div className={css.slideInnerField} style={{left:this.state.shiftLeft}}>
						<div className={css.slidePictures} ><img alt='slide1' src={slide1}/></div>
						<div className={css.slidePictures} ><img alt='slide2' src={slide2}/></div>
						<div className={css.slidePictures} ><img alt='slide3' src={slide3}/></div>
					</div>
				</div>
				<div className={css.textBlockRight}><div className={css.innerTextBlock}>Битва</div></div>
			</div>
		);
	}
	
}


export default function Header(props){
	return(
			<div className={css.content}>
				<div className={css.headerContent}>
					<SlideBlock />
					<Navigation isLogined={props.isLogined}/>
				</div>
			</div>
		)
}