import React from 'react';
import css from '../GridBoxUser/GridField.module.css';


let  GridItem=React.memo(({id,handleOnClick,index,style,hited})=>{
	// console.log('render every square...');
	return (	<div 	id={id} 
						onClick={handleOnClick} 
						key={index} 
						className={style}>
					<div id={id} className={hited}>
					</div>
				</div>
		   );
});


export default class GridFieldContainer extends React.PureComponent{

	constructor(props){
		super(props);
		this.itemsArr=new Array(100).fill('');
		this.onGridClick=props.onGridClick;
		this.updateFlag=false;
	}

	handleOnClick=(e)=>{
		if(this.props.active) this.onGridClick(e);
		
	}
	getStyle(id){
		let style=css.empty;
		if(this.props.userGrid && this.props.selectedShipItem>0){
			if(this.props.fieldUser[id]===1){
				if(this.props.arrAroundArea.indexOf(id)!==-1 && this.props.checkArround===true)	style=css.shipError;
				else style=css.ship;
			} 
			if(this.props.fieldUser[id]===2) style=css.shipHited;
			if(this.props.fieldUser[id]===3) style=css.emptyHited;
			if(this.props.arrAbstractShip.indexOf(id)!==-1) style=`${style} ${css.abstractShip}`;
			else if(this.props.arrAroundArea.indexOf(id)!==-1 && this.props.checkArround===true && this.props.fieldUser[id]!==1) {
					style=`${style} ${css.abstractArea}`
				}
		}
		else{
			// if(this.props.fieldUser[id]===1) style=css.ship;
			if(this.props.fieldUser[id]===2) style=css.shipHited;
			if(this.props.fieldUser[id]===3) style=css.emptyHited;
		}
		return style	
	}

	render(){
		// console.log('rerender field...')
		let style='';
		if(this.props.active===true) {style=css.cursor}
		this.itemsArr.forEach((i,idx)=>{
			 this.itemsArr[idx]=<GridItem style={`${(this.props.cursorId===idx)? style:''} ${this.getStyle(idx)} ${css.fieldItem}`}
								  hited={(this.props.fieldUser[idx]===2 || this.props.fieldUser[idx]===3)? css.hited:''}
								  id={idx+1}
								  key={idx}
								  handleOnClick={this.handleOnClick}/>  
			 	
			});
		this.updateFlag=true;
		let Fragment=React.Fragment;
		return(
				<Fragment>
					{this.itemsArr}
				</Fragment>

			);
	}
}

