
const arrOfcheck=[[0,1,10,11],[0,-1,9,10],[0,1,-9,-10],[0,-1,-10,-11],
				  [0,1,-1,9,-9,10,-10,11,-11],
				  [0,-1,1,9,10,11],[0,1,-1,-9,-10,-11],[0,-1,-10,-11,9,10],[0,1,10,11,-9,-10]
				 ];

const SHIP_POINT=1;
// const SHIP_HITED=2;
// const FILL_POINT=3;
const EMPTY_POINT=0;



export let localAPI={
// let localAPI={
__shipRotate(shipArr){
	return shipArr.map(el=>el*10);
},
getTimeTick(time){
	if(time!=='Время вышло.'){
		let newTime=time.split(':');

		if(Number(newTime[1])+1>59) {
			newTime[1]='00';
			newTime[0]=((Number(newTime[0])+1).toString().length<2)? ('0'+(Number(newTime[0])+1).toString()):(Number(newTime[0])+1).toString();
		}
		else{
			newTime[1]=((Number(newTime[1])+1).toString().length<2)? ('0'+(Number(newTime[1])+1).toString()):(Number(newTime[1])+1).toString();
		}
		if(Number(newTime[0])>10) return 'Время вышло.';

	return newTime.join(":");
	}
	return time;
},
__getPointSecs(timeString){
	let timeSec=0;
	let timePoints=3700;
	let	timeArr=timeString.split(':');
		timeSec=Number(timeArr[0])*60+Number(timeArr[1]);
	if(timeSec) return timePoints-timeSec;
	else return 0;
},
calculatePoints(userItem){
	let points=0;
		if(Number(userItem.shuts)!==0) points=4500-Number(userItem.shuts)*50;
		points+=this.__getPointSecs(userItem.time);
	return points;
},
sortUsersByPlace(usersItems){
	let result=[...usersItems.sort((a,b)=>b.points-a.points)];
	return result;
}
,
addUserToList(Items,newUser){
	// let idx=undefined;
	let result=Items.some((it,x)=>{if(newUser.name===it.name && newUser.gender===it.gender && newUser.email===it.email){
									  return true;	
									} 
					else return false;
				});
	return result;
},

checkWinner(userShips){
let result=false;
	result=userShips.some(i=>{
		return i.ship.some(it=>{
			return it.some(el=>el!==null)
		});
	});
// console.log('winner...',!result);
return !result;
},

getVirtualPoints(posId){
let arrFilter=[[1,10],[-1,10],[1,-10],[-1,-10],
			   [1,-1,10,-10],
			   [1,-1,10],[1,-1,-10],[1,10,-10],[-1,10,-10]];
let arrId;
let virtualPoints=[];
			arrId=4;
			if(posId===0) arrId=0;	
			if(posId===9) arrId=1;
			if(posId===90) arrId=2;
			if(posId===99) arrId=3;
			if(posId>0 && posId<9) arrId=5;
			if(posId>90 && posId<99) arrId=6;
			if((posId)%10===0 && posId>9 && posId<90) arrId=7;
			if((posId-9)%10===0 && posId>9 && posId<90) arrId=8;

			arrFilter[arrId].forEach(i=>{virtualPoints.push(posId+i)});

	return virtualPoints;
},

getHitPosition(fieldUser,virtualPoints,memoKiledPoints){
let repeat=true;
let posId=null;
let x=[];
while(repeat && x.length<99){
	if(virtualPoints.length===0 && memoKiledPoints.length===0) posId=Math.floor(Math.random()*100);
	else{
		if(virtualPoints.length===0) virtualPoints=this.getVirtualPoints(memoKiledPoints.pop());
		posId=virtualPoints.pop();
	} 

	if(x.indexOf(posId)===-1){
		  if(fieldUser[posId]===EMPTY_POINT || fieldUser[posId]===SHIP_POINT){
		  	repeat=false;
		  } 
		  else x.push(posId);
		}
	}	
return (x.length>=99)? null:posId;
},

fillKilledShip(coordinates,fieldUser){
let {arrAroundErea}=this.findAreas(coordinates);

   //вычленяем координаты корабля из окружения и результат заносим в поле
	arrAroundErea.filter(i=>(coordinates.indexOf(i)===-1)? true:false)
				 .forEach(i=>fieldUser[i]=3);
},

setItemShipToField(stateCopy){
	stateCopy.shipAbstractArea.forEach((it,idx)=>{
					stateCopy.fieldUser[it]=SHIP_POINT;
					let j=stateCopy.shipsCount[stateCopy.selectedShipItem-1]-1;
					//заносим значения координат в нужные корабли
					stateCopy.userShips[stateCopy.selectedShipItem-1].ship[j][idx]=it;
					stateCopy.userShips[stateCopy.selectedShipItem-1].pos[j][idx]=it;
					});
	return
},

findHitedShip(userShips,position){
let currentShip=[];
let coordinates=null;
let result=userShips.some(i=>{

			  return i.ship.some((it,idx)=>{
			  	let pos=it.indexOf(position);
			  	if(pos!==-1){ 
			  		it[pos]=null;
			  		currentShip=it;
			  		coordinates=i.pos[idx];
			  	   return true
			  	}
				 return false;	
				})
			});
 
 	return [currentShip,coordinates,result];
},

findAreas(newShip,rotate=true,posId=0){
	let idArr;
	var arrAroundErea=[];
	var arrAbstractShip=newShip.map((i,count)=>{
				idArr=4;
				if(posId+i===0) idArr=0;
				if(posId+i===9) idArr=1;
				if(posId+i===90) idArr=2;
				if(posId+i===99) idArr=3;
				if(posId+i>0 && posId+i<9) idArr=5;
				if(posId+i>90 && posId+i<99) idArr=6;
				if((posId+i)%10===0 && posId+i>9 && posId+i<90) idArr=8;
				if((posId+i-9)%10===0 && posId+i>9 && posId+i<90) idArr=7;

			if(rotate===false){
				if(Math.floor(posId/10)===Math.floor((posId+i)/10)){
					arrOfcheck[idArr].forEach(it=>{
					let item=posId+i+it;
					//проверка если нет выхода за пределы поля и нет повторяющихся элементов
						if(item<100 && item>=0 && arrAroundErea.indexOf(item)===-1)
					  	  arrAroundErea.push(item)
					});
				return posId+i;
				}
			}
			else{
					arrOfcheck[idArr].forEach(it=>{
					let	item=posId+i+it;
						//проверка если нет выхода за пределы поля и нет повторяющихся элементов
						if(item<100 && item>=0 && arrAroundErea.indexOf(item)===-1)
						    arrAroundErea.push(item)
					});
				return (posId+i<100)?posId+i:undefined;
				}
			return undefined;
		});
	return {arrAroundErea,arrAbstractShip}
},

findAroundItems(fieldUser,userShip,cursor,isUser=false,isRotate=false){

	const TRY_COUNT=20;
	let newShip;
	let posId;
	let rotate;
	let loop=true;
	let isPossible=false;

	let count=0;
	if(!isUser){
		while(loop && count<TRY_COUNT){
			posId=Math.floor(Math.random()*100);
			rotate=(Math.round(Math.random())===1)? true:false;

			if(rotate===true) newShip=this.__shipRotate(userShip);
			else newShip=userShip.map((i,x)=>x);
			var {arrAroundErea,arrAbstractShip}=this.findAreas(newShip,rotate,posId);
			//повторяем пока не найдётся свободное место для позиционирования
			loop=arrAbstractShip.some(i=>i===undefined) || arrAroundErea.some(i=>fieldUser[i]!==EMPTY_POINT);	
			count++;	
		}
		isPossible=(count===TRY_COUNT)? false:true;
		// console.log("is possible:",isPossible);
		return ({arrAbstractShip,arrAroundErea,isPossible});
	}
	else{
			if(isRotate===true) newShip=this.__shipRotate(userShip);
			else newShip=userShip.map((i,x)=>x);

			let {arrAroundErea,arrAbstractShip}=this.findAreas(newShip,isRotate,cursor);
			isPossible=arrAbstractShip.some(i=>i===undefined) || arrAroundErea.some(i=>fieldUser[i]!==EMPTY_POINT);
			//return => {arrAroundErea,arrAbstractShip,result};
			return ({arrAbstractShip,arrAroundErea,isPossible:!isPossible});
	}
	
},	

findNextItemButton(arr,id){

	let m=arr.map(i=>i===0? false:true).indexOf(true);
	return (m===-1)? false:m;
},

clearField(fieldUser){	
	return fieldUser.map(i=>EMPTY_POINT);
}

} 

